// Navbar
.navbar {
    .navbar-brand {
        span {
            font: {
                size: 25px;
                weight: 600
            }
            margin-top: -3px;
            margin-left: 10px;
        }
    }
    .collapse {
        .navbar-nav {
            .nav-item {
                .nav-link {
                    font-size: 12px;
                    font-weight: 800;
                    text-transform: uppercase;
                    padding: 4px 25px;
                    letter-spacing: 2px;
                    border: 1px solid transparent;
                    border-radius: 4px;
                    
                }
            }
        }
    }
}